<template>
    <div class="row" v-if="upd">
        <div class="col-md-12" style="margin-top:-25px">
            <h6><span class="badge badge-secondary" style="border-radius:0px;">Período para filtro</span></h6>
            <div class="card">
                <div class="content pb-0">
                    <div class="row form-group">
                        <div class="col col-md-3" style="margin-top:-10px;">
                            <input type="nome" id="nf-nome" name="nf-nome" value="01/01/2021"
                                placeholder="" class="form-control">
                        </div>
                        <div class="col col-md-3" style="margin-top:-10px;">
                            <input type="nome" id="nf-nome" name="nf-nome" value="01/05/2021" 
                                placeholder="" class="form-control">
                        </div>
                        <div class="col col-md-5" style="float:right;"></div>
                        <div class="col col-md-1" style="float:right;margin-top:-10px;">
                            <button type="button" class="btn btn-primary"><i class="fa fa-search"></i></button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <info
            icon="icon-md pe-7f-loop"
            bgclass="bg-flat-color-2"
            symbol="R$"
            counter=0
            title="Replanejado"
        >
        </info>

        <info
            icon="icon-md pe-7f-graph1"
            bgclass="bg-flat-color-3"
            symbol="%"
            :counter="Math.round((vEntregue * 100) / (vEntregue + vPendente) * 100) / 100"
            hasPercentage="yes"
            title="% Entregue"
        >
        </info>

        <info
            icon="icon-md pe-7f-check"
            bgclass="bg-flat-color-5"
            symbol="R$"
            :counter="vEntregue"
            title="Entregue"
        >
        </info>

        <info
            icon="icon-md pe-7f-clock"
            bgclass="bg-flat-color-4"
            symbol="R$"
            :counter="vPendente"
            title="Pendente"
        >
        </info>
        
        <!--
        <info-chart
            icon="icon-lg pe-7f-cart"
            symbol="%"
            hasPercentage="yes"
            bgclass="bg-flat-color-6"
            counter=85
            canvasId="flotBar1"
            title="Sales Increase"
        >
        </info-chart>

        <info-chart-two
            icon="icon-lg pe-7f-cart"
            bgclass="bg-flat-color-2"
            counter=1490
            canvasId="flotLine1"
            title="New Users"
        >
        </info-chart-two>
        -->

        <!--
        <RealTime title="Real time"/>
        -->
        <!--
        <EarningStats/>
        <Download/>
        <Revenue/>
        -->

        <div class="col-md-4">
            <aside class="profile-nav alt">
                <section class="card">
                    <div class="card-header user-header alt bg-dark">
                        <div class="media">
                            <div class="media-body">
                                <h2 class="text-light display-6">Programas</h2>
                                <p>Pendentes | Entregues</p>
                            </div>
                        </div>
                    </div>
                    <ul class="list-group list-group-flush">
                        <li class="list-group-item" v-for="(programa) in programas" :key="programa.id">
                            <a href="#">{{ programa.dados.nome }}
                            </a>
                            <div class="pull-right">
                                <span class="badge badge-danger">{{ programa.dados.totalPendente }}</span> | 
                                <span class="badge badge-success">{{ programa.dados.totalEntregue }}</span>
                            </div>
                        </li>
                    </ul>
                </section>
            </aside>
        </div>

        <Traffic title="Real time"/>

        <div class="col-sm-6">
            <card header-text="Gráfico 1" class="mb-4">
                <div class="chart-wrapper mb-4" >
                    <grafico-2 style="height:375px"/>
                </div>
            </card>
        </div>

        <div class="col-sm-6">
            <card header-text="Pedente por PROJETO" class="mb-4">
                <div class="chart-wrapper mb-4" >
                    <grafico-3 
                        :data="g3Data"
                        :labels="g3Labels"
                        style="height:375px"/>
                </div>
            </card>
        </div>

        <div class="col-sm-12">
            <card header-text="Gráfico 3" class="mb-4">
                <div class="chart-wrapper mb-4" >
                    <grafico-1 style="height:375px"/>
                </div>
            </card>
        </div>
    </div>

</template>

<script>
import Info from './dashboard/InfoBox.vue';
import InfoChart from './dashboard/InfoBoxChart.vue';
import InfoChartTwo from './dashboard/InfoBoxChart2.vue';
import RealTime from './dashboard/RealTime.vue';
import Traffic from './dashboard/Traffic.vue';
import EarningStats from './dashboard/EarningStats.vue';
import Download from './dashboard/Download.vue';
import Revenue from './dashboard/Revenue.vue';
import Grafico1 from './graficos/Grafico1.vue';
import Grafico2 from './graficos/Grafico2.vue';
import Grafico3 from './graficos/Grafico3.vue';


export default{
    name: 'dashboard',
    components: {
        Info,
        InfoChart,
        InfoChartTwo,
        RealTime,
        Traffic,
        EarningStats,
        Download,
        Revenue,
        Grafico1,
        Grafico2,
        Grafico3
    },
    data() {
        return {
            programas: [],
            entregaveis: [],
            tPendente: 0,
            tEntregue: 0,
            vPendente: 0,
            vEntregue: 0,
            upd: false,
            g3Data: [],
            g3Labels: [],
            itensProcessados: 0
        }
    },
    watch: {
        itensProcessados() {
            if (this.programas.length == this.itensProcessados) {
                this.recarregar()
            }
        }
    },
    methods: {
        recarregar() {
            let dataG3 = {}
            console.log(this.programas)
            this.programas.forEach((element, index, array) => {
                let nomeProjeto = element.dados.projeto.nome
                if (!dataG3[nomeProjeto]) {
                    dataG3[nomeProjeto] = 0;
                }
                dataG3[nomeProjeto] += element.dados.valorPendente
            });

            console.log(dataG3)

            Object.entries(dataG3).forEach(([key, value]) => {
                this.g3Data.push(value);
                this.g3Labels.push(key);
            });

            console.log(this.g3Data)

            this.upd = false;
            this.$nextTick().then(() => {
                this.upd = true;
            })
        }
    },
    created() {
        this.$http.get('programas.json').then(res => {
            Object.entries(res.data).forEach(([key, value]) => {
                    this.programas.push({
                        id: key,
                        dados: value
                    });
            });
        }).then(res => {
            this.programas.forEach((element, index, array) => {
                let te = 0;
                let tp = 0;
                let perc = 0;
    
                let ve = 0;
                let vp = 0;

                let key = element.id

                this.$http.get('entregaveis/' + key + '.json').then(res => {
                    if (res.data) {
                        Object.entries(res.data).forEach(([key, value]) => {
                            if (value.concluido == '1') {
                                te += 1;
                                ve += parseInt(value.valor.replace(/\./g,'').replace(/\,/g,'.'))
                            } else {
                                tp += 1;
                                vp += parseInt(value.valor.replace(/\./g,'').replace(/\,/g,'.'))
                            }
                        })
                        perc = (te * 100) / (te + tp)
                        perc = Math.round(perc * 100) / 100;
                    }
    
                    element.dados.perc = perc
                    element.dados.totalPendente = tp;
                    element.dados.totalEntregue = te;
                    element.dados.valorPendente = vp;
                    element.dados.valorEntregue = ve;
    
                    this.tPendente += tp
                    this.tEntregue += tp
                    this.vPendente += vp
                    this.vEntregue += ve

                    this.itensProcessados += 1;
                });
            });
        });
    }
}

</script>

<style>
    .card .card-left {
        width: 80%;
    }

    .card .card-right {
        width: 20%;
    }

    .icon-md {font-size: 1.7em;}
</style>


