<script>
import { Pie } from 'vue-chartjs'

export default({
  extends: Pie,
  props: {
    data: [],
    labels: []
  },
  mounted () {
    this.renderChart({
       datasets: [{
            label: 'Valor da linha 1',
            data: this.data,
            type: 'pie',
            backgroundColor: ["#003f5c", "#2f4b7c", "#665191", "#a05195", "#d45087", "#f95d6a", "#ff7c43", "#ffa600"],
           // backgroundColor: ["#004c6d", "#006083", "#007599", "#008bad", "#00a1c1", "#00b8d3", "#00cfe3", "#00e7f2", "#00ffff"]
        }],
        labels: this.labels
    },
    {
      maintainAspectRatio: false,
      legend: {
        display: true,
        position: 'bottom'
      }
    })
  }
})
</script>


