<script>
import { Line } from 'vue-chartjs'

export default({
  extends: Line,
  mounted () {
    this.renderChart({
       datasets: [{
            label: 'Valor da linha 1',
            data: [30, 120, 80, 70, 90, 130, 150, 150, 100, 120, 110],
            type: 'line',
            fill: 'origin',
            borderColor: "#42b983",
            backgroundColor: "#42b983"
        }],
        labels: ['2015', '2016', '2017', '2018', '2019', '2020', '2021', '2022', '2023', '2024', '2025']
    },
    {
      maintainAspectRatio: false,
      legend: {
        display: true,
        position: 'bottom'
      }
    })
  }
})
</script>


