<script>
import { Bar } from 'vue-chartjs'

export default({
  extends: Bar,
  mounted () {
    this.renderChart({
       datasets: [{
            label: 'Valor da linha 1',
            data: [80, 80, 80, 80, 80, 80, 80, 80, 80, 80, 80],
            type: 'line',
            fill: false,
            borderColor: "#42b983",
            backgroundColor: "#42b983"
        }, {
            label: 'Valor da linha 2',
            data: [60, 60, 60, 60, 60, 60, 60, 60, 60, 60, 60],
            type: 'line',
            fill: false,
            borderColor: "#F53704",
            backgroundColor: "#F53704"
        },{
            label: 'Coluna 1',
            data: [0, 0, 0, 0, 0, 0, 0, 30, 50, 70, 90],
            backgroundColor: "#C6C6C6"
        },{
            label: 'Coluna 2',
            data: [0, 0, 0, 0, 0, 0, 0, 25, 45, 65, 85],
            backgroundColor: "#7F7F7F"
        },{
            label: 'Coluna 3',
            data: [0, 0, 0, 0, 0, 0, 0, 10, 20, 30, 40],
            backgroundColor: "#404AFF"
        }],
        labels: ['2015', '2016', '2017', '2018', '2019', '2020', '2021', '2022', '2023', '2024', '2025']
    },
    {
      maintainAspectRatio: false,
      legend: {
        display: true,
        position: 'bottom'
      }
    })
  }
})
</script>


